.header-main {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9;
  box-shadow: 0px 6px 15px #0000000F;

  .header-logo{
    width: 45px;
  }

  .header-icon{
    width: 22px;
    height: 22px;
  }

  .location-pin{
    width: 12px;
  }

  .pin-input{
    width: 160px;
    border-radius: 5px;
    border:1px solid var(--yellow);
    padding: 0 10px;
    font-size: 14px;
    font-family: 'Roboto-medium';
    color: var(--black);
    @media screen and (max-width: 1024px ){
      width: 160px;
    }
  }
  .searchIcon{
   position: absolute;
    right: 15px;
    top: 4px;
  }
  .suggestion-box{
    width: 100%;
    position: absolute;
    border-radius: 10px;
    background: var(--white);
    z-index: 2;
    padding: 10px 15px;
    li{
      white-space: nowrap !important;
      overflow: hidden !important;
      text-overflow: ellipsis !important;
      span{
        white-space: nowrap !important;
        overflow: hidden !important;
        text-overflow: ellipsis !important;
      }
    }

  }
  .suggestion-box-height{
    width: 100%;
    position: absolute;
    border-radius: 10px;
    background: var(--white);
    overflow-y: auto;
    z-index: 2;
    padding: 10px 15px;
    li{
      white-space: nowrap !important;
      overflow: hidden !important;
      text-overflow: ellipsis !important;
      span{
        white-space: nowrap !important;
        overflow: hidden !important;
        text-overflow: ellipsis !important;
      }
    }
  }

  .login-drop{
    right: 0 !important;
    left: unset;
  }

  .input-style {
    border: 1px solid var(--gray);
    border-radius: 25px;
    padding: 0 40px 0 20px;
    width: 375px;
    height: 35px;
    display: flex;
    align-items: center;
    font-family: 'Roboto-regular';
    font-size: 14px;
    &:focus{
      border-color: var(--yellow);
    }
    &::placeholder {
      color: var(--dark-gray);
    }

    @media screen and (max-width: 991px) {
      width: 100%;
    }
  }

  .sub-header {
    a {
      padding-bottom: 3px;

      &:hover {
        border-bottom: 2px solid var(--yellow);
      }
    }

    a.active {
      border-bottom: 2px solid var(--yellow);
    }

    @media screen and (max-width: 991px){
      white-space: nowrap;
      overflow-x: scroll;
      li{
        margin-bottom: 3px;
      }
    }
  }

  .profile-img {
    height: 30px;
    width: 30px;
    border-radius: 100%;
  }

  .cart-count {
    color: white;
    background-color: #ea504e;
    border-radius: 20px;
    display: block;
    font-size: 12px;
    font-weight: 500;
    height: 20px;
    left: 15px;
    line-height: 9px;
    min-width: 20px;
    padding: 5px;
    position: absolute;
    text-align: center;
    top: -9px;
  }
}
.custom-dropdowm {
  .dropdown-toggle::after{
    background:red;
    display: none !important;
  }
}
